.thumbnail{
    display: flex;
    width:100vw;
    

}
.blank{
    height:50px;
    line-height:50px;
    text-align:center;
    color:transparent;
    text-shadow: 0 0 0 rgb(168, 138, 138);
    -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
    background-color:#46575D;
    font-size:0.8rem;
}
.thumbnailimage{
    height:90vh;
    width:50vw;
    object-fit: cover;
}
.info{
    display:flex;
    flex-direction:column;
    justify-content: space-around;
    align-items: center;
    padding:2rem;
    width: 50vw;
}
.info .title{
font-size:3rem;
font-family: 'Lobster', cursive;
}
.info .shortdes{
    font-family: 'Dancing Script', cursive;
    font-size: 1.8rem;
    text-align:center;
}
.button{
    text-decoration:none;
    width:80px;
    height:30px;
    display: inline-block;
    background-color:bisque;
    text-align:center;
    line-height:30px;
    border-radius: 8px;
    transition: all 0.1s ease-in-out;
    color:black;
}
.button:hover{
    transform: scale(1.05);
    opacity:0.8;

}