.navbar {
    display: flex;
    justify-content:flex-start;
    align-items:center;
    background-color:#413333;
    height:50px;
    width:100%;
    padding-right: 15px;
}
.menu{
    display: flex;
    justify-content: center;
    align-items:center;
    font-size: 1.2rem;
    background-color:#B2AFAF;
    width: 76%;
    height:60%;
    border-radius:28px;
    font-family: 'Rochester', cursive;
    font-weight:600;
    position: relative;
    left: 62px;



}
.signing.signoutcolor{
    background-color: #802629;
}
.menuitem{
    color: #000000;
   display: inline-block;
   text-align:center;
   width:70px;
   line-height:25px;

}
.menuitem:hover{
    transition-duration: 0.15s;
    transition-timing-function: ease-in-out;
    transform: scale(1.17);
    color: #4D4646;
    cursor: pointer;

}
.cowandmilk{
    font-size: 1.5rem;
    position: relative;
    bottom:4px;
margin-left: 30px;
margin-right:1%;
}
.cowandmilk:hover .milk{
    transform:rotate(45deg) translate(2px, -7px);
    cursor: pointer;
}
.cowandmilk:hover .cow{
    transform:rotate(10deg);
    cursor: pointer;
}
.cow{
    display:inline-block;
    transform:rotate(0deg);
    transition:all 0.5s ease-in-out;
}
.milk{
    display:inline-block;
    font-size:1rem;
    transform:rotate(0deg) translate(0px, 0px); 
    transition:all 0.5s ease-in-out;

}
.linkitem {    
    text-decoration: none;
    margin: 0 30px;
    display:flex;
    align-items: center;
    height:100%;
}

