.frame{
    width:100%;
    height:500px;
    background-image: url('https://i.imgur.com/hm4JlVi.jpg');
    background-position-y: 60%;
    background-attachment: fixed;
    background-size: 100%;
    background-repeat: no-repeat;
}
.bigtitle{
    font-family: 'Fruktur', cursive;
    color: #DCD2D2;
    font-size: 4.5rem;
    margin-left: 770px;
    text-shadow: 9px 9px 2px #272F26;

    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #131D0C;
}
.filmtext{
    max-width:450px;
    font-size:1.3rem;
    color:#F5ECEC;
    text-align:center;
    font-family: 'Montaga', serif;
    margin-left: 635px;
    margin-top:50px;
}
.readmoreframe{
position: relative;
left: 810px;
top:85px;
width:9%;
height:32px;
background-color: #4A757E;
display:flex;
justify-content: center;
align-items:center;
border-radius:28px;}

.readmoreframe:hover{
    transition: 0.15s;
    transition-timing-function: ease-in-out;
    transform: scale(1.06);
    color: #232020;
    cursor: pointer;
}
.pressme{
    text-decoration: none;
}
.readmore{
    color:#000000;
}
